import React, { Component, useState, useEffect } from 'react';
import axsion from 'axios';/* 'axsion' connect HTTP request with backend*/
import '../scss/_home.scss';
import { PostMansonry } from './';

const PostsUtill = () => {

    const [postsUtill, setPosts] = useState([]);

    const fetchPosts = () => {
        axsion.get("api/v1.0/post/get").then(response => {
            console.log(response);
            setPosts(response.data);
        }).catch(function (error) {
            if (error.response) {
                console.log(error.response.data);
                console.log(error.response.status);
                console.log(error.response.headers);
            }
        });
    };

    useEffect(() => {
        fetchPosts();
    }, []);

    return (
        <section className="container-post">
                <PostMansonry postsUtill={postsUtill} columns={3} />
        </section>
    );
};


class Post extends Component {
    render() {
        return (
            <main role="main" className="main-block col-md-9 ml-sm-auto col-lg-10 px-md-4">
                <div className="d-flex justify-content-between flex-wrap flex-md-nowrap align-items-center pt-3 pb-2 mb-3 border-bottom">
                    <h3 className="h3 noselect">Posts</h3>
                </div>
                <PostsUtill />
            </main>
        )
    }
}


export default Post;
