import React from 'react';

function truncateWithEllipses(text, max) {
    return text.substr(0, max - 1) + (text.length > max ? '...' : '');
}

export default function MasonryPost({ postsUtill }) {

    return (
        <div className="masonry-post">
            <div className="card">
                <img className="card-img-top" src={`data:image/jpeg;base64,${postsUtill.image}`} alt="Card image cap" />
                <div className="card-body">
                    <h5 className="card-title">{postsUtill.title}</h5>
                    <p className="card-text"> {truncateWithEllipses(postsUtill.description, 260)}</p>
                    <p className="card-text"><small className="text-muted"> {postsUtill.zonedDateTime}</small></p>
                </div>
            </div>
        </div>
    )
}
