import React from 'react';
import { MasonryPost } from './';

export default function PostMansonry({ postsUtill, columns }) {
    return (
        <section className="masonry" style={{ gridTemplateColumns: `repeat(${columns}, minmax(275px, 1fr))` }}>
            {postsUtill.sort((a, b) => a.zonedDateTime < b.zonedDateTime ? 1 : -1).map((postsUtill, index) =>
                <MasonryPost {...{ postsUtill, index, key: index }} />)
            }
        </section>
    )

}